@import "src/assets/variables";
@import "src/assets/@mixin";

.animate_top_section {
  background: $backgroundGradient;
  overflow: hidden;
  position: relative;
  margin: -30px 0;
  @media screen and (min-width: $md3+px) {
    display: flex;
  }
}

.top_section_text_block {
  flex: 0 0 100%;

}

.animation_text_content {
  //padding: 140px 0 243px ;
  width: 50%;
  h3 {
    color: $white;
    font-style: normal;
    font-weight: 400;
    position: relative;
    text-decoration: underline;
    text-underline-offset: 8px;
    text-decoration-thickness: 2px;
    text-decoration-color: $white1;

    @include adaptive-font(90,40);
    @include adaptive-value("line-height",140,60,1);


  }

  p {
    color: $white;
    font-style: normal;
    font-weight: 400;
    line-height: 54px; /* 54px */
    margin-top: 30px;
    max-width: 800px;

    @include adaptive-font(45,22);
    @include adaptive-value("line-height",54,26,1);
    @include adaptive-value("margin-top",30,15,1);

    @media (max-width: $md2 + px) {
      max-width: 500px;
    }
  }
  @media (max-width: $md2 + px) {
    width: 100%;

  }
  @include adaptive-value("padding-top",170,50,1);
  @include adaptive-value("padding-bottom",150,63,1)

}
.animation_header_image {
  position: absolute;
  right: -600px;
  top: -200px;


  img{
    max-width: none;
    max-height: none;
    animation: pulse 10s linear infinite;
  }


}
@keyframes pulse {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
